<template>
  <div class="input-wrapper">
    <input
      ref="text"
      class="input"
      placeholder=" "
      :value="value"
      @change="changeHandler"
      type="text"
    />
    <ion-text
      :class="{ inputTextBlue: textBlue === true }"
      class="input-text"
      @click="onFocusText()"
      >{{ name }}<ion-text v-if="required" class="dot">*</ion-text></ion-text
    >
  </div>
</template>
<script>
import { defineComponent } from "vue";
import { IonText } from "@ionic/vue";

export default defineComponent({
  components: {
    IonText,
  },
  setup() {},
  methods: {
    onFocusText: function () {
      console.log("focus");
      this.$refs.text.focus();
    },
  },
  data() {
    return {
      value2: this.value,
      changeHandler2: this.changeHandler,
    };
  },
  computed: {
    inputValue: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit("updated", val);
      },
    },
  },
  name: "inputView",
  props: {
    required: Boolean,
    textBlue: Boolean,
    name: String,
    value: String,
    changeHandler: Function,
  },
});
</script>

<style scoped>
.input {
  border-radius: 8px;
  padding: 15px;
  width: 100%;
  border: solid 1px #e0e0e0;
  margin-bottom: 15px;
  --padding-start: 15px;
  --padding-bottom: 14px;
  --padding-top: 14px;
  --placeholder-color: #9e9e9e;
  --placeholder-font-weight: 400;
}

.input:focus {
  outline: none !important;
  border: solid 1px #62d0ce;
  caret-color: #000;
}
.input-wrapper {
  position: relative;
  width: 100%;
}
.input-text {
  z-index: 0;
  padding-left: 15px;
  position: absolute;
  left: 1px;
  top: 15px;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
}
.inputTextBlue {
  color: #0378b4;
  font-weight: 700;
}

input:not(:placeholder-shown) + ion-text {
  display: none;
}

.dot {
  color: #62d0ce;
}
</style>